
<template>
    <v-container fluid class="fill-height app"  :style="{ background: background }">
      <v-row>
        <v-col cols="12" sm="8" class="border appChat container mx-auto px-8 lg:px-48 ">
          <v-app-bar color="rgba(0,0,0,0)" flat>
            <v-badge
              bordered
              bottom
              color="green"
              dot
              offset-x="11"
              offset-y="13"
            >
              <v-avatar class="mt-n5 mr-2" size="50" elevation="10">
                <img
                  v-if="OnWrite === true"
                  src="https://cdn.vuetifyjs.com/images/lists/1.jpg"
                />
                <img v-else src="../assets/robot.jpg" />
              </v-avatar>
            </v-badge>
            <v-toolbar-title class="mb-4 text-2x1 font-bold">
              <div v-if="OnWrite === true">you</div>
              <div v-else>GPT</div>
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-title class="mb-4 text-2x1 font-bold">
              Member:
            </v-toolbar-title>
            <v-avatar class="mt-n5 mr-2" size="50" elevation="10">
              <img src="../assets/images.jpg" />
            </v-avatar>
          </v-app-bar>
          <div class="container-chat">
            <v-app-bar 
              v-for="Message in Messages"
              :key="Message.id"
              color="rgba(0,0,0,0)"
              flat
              class="mb-16 Messages"
              :class="{
                rightSide:
                  Message.sender === 'chatGPT-1' || Message.sender === 'server',
                leftSide: Message.sender === 'chatGPT-2'
              }"
            >
              <v-card
                :class="{
                  messageRight:
                    Message.sender === 'chatGPT-1' || Message.sender === 'server',
                  messageLeft: Message.sender === 'chatGPT-2'
                }"
                class="mt-10"
                max-width="350px"
              >
                <v-list-item three-line>
                  <v-list-item-content>
                    <div class="mb-4">{{ Message.text }}</div>
                    <v-list-item-subtitle>
                      {{ Message.sender }} {{
                        new Date().getHours().toString().padStart(2, '0')
                      }}:{{ new Date().getMinutes().toString().padStart(2, '0') }}
                    </v-list-item-subtitle>
                  </v-list-item-content>
                </v-list-item>
              </v-card>
            </v-app-bar>

            <div ref="bottomElement"></div>
          </div>

          <div class="WriteMessage">
            <v-app-bar class="container mx-auto px-800 lg:px-4800" color="rgba(0,0,0,0)" flat>
              <v-text-field
                v-model="text"
                :append-outer-icon="OnWrite ? 'mdi-send' : runing ? 'mdi-pause' : 'mdi-play'"
                filled
                clear-icon="mdi-close-circle"
                clearable
                label="Message"
                type="text"
                @click="WriteMessage"
                @keyup.enter="sendMessage"
                @click:append-outer="sendMessage"
                @click:clear="clearMessage"
              ></v-text-field>
            </v-app-bar>
          </div>
        </v-col>
      </v-row>
    </v-container>
</template>


<script>

import axios from 'axios';
//import ModalLogin from '@/components/ModalLogin.vue'



export const Messages = [];
export default {
  components: {
  //  ModalLogin,
  },
  data () {
    return{
      runing: true,
      isRotating: true,
      lestSender: {name:'chatGPT-1', address: 'http://localhost:5000/openAI1'},
      Messages: [], // הוספת מערך ריק להצגת הודעות באתר
      newMessage: {},
      newAnswer: {},
      Answers: [],
      text: 'Type a message here to start',
      OnWrite: true,
      MessageForSend: 'hi',
      MessageNumber: 1,
      AnswerNumber: 2,
      openAi1: {name:'chatGPT-1', address: 'http://localhost:5000/openAI1'},
      openAi2: {name:'chatGPT-2', address: 'http://localhost:5000/openAI2'},
      response: null,
      background: this.$vuetify.theme.themes.dark.background

    }
  },
  computed: {
    theme() {
      return this.$vuetify.theme.dark ? "dark" : "light";
    },
  },
  methods: {
    stopRotation() {
      this.isRotating = false;
    },
    runServer(){
  console.log('runServer')
  if( this.runing === false){
    this.runing = true
  }else{
    this.runing = false
  }
},
    sendMessage() {
      if(this.OnWrite){
        this.runing = true
        if(this.text !== '' ){
         this.newMessage = {
        id: this.MessageNumber,
        text: this.text,
        sender: 'you',
      };
      this.Messages.push(this.newMessage); // הכנסת ההודעה למערך ההודעות ב-Vue
      this.$refs.bottomElement.scrollIntoView({ behavior: 'smooth' });
      this.MessageNumber += 2;
      this.text = '';
      this.GPTconversation()
    }
  }else if( this.runing === true){
    console.log('stopServer')
    this.runing = false;
    this.newMessage = {
        id: this.MessageNumber,
        text: 'השיחה הופסקה',
        sender: 'server',
      };
      this.Messages.push(this.newMessage); // הכנסת ההודעה למערך ההודעות ב-Vue
      this.$refs.bottomElement.scrollIntoView({ behavior: 'smooth' });
      this.MessageNumber += 2;
      this.text = 'Type a message here to start';
      this.lestSender.address === this.openAi1.address;
  }else{
    this.runing = true
    this.newMessage = {
        id: this.MessageNumber,
        text: 'השיחה ממשיכה',
        sender: 'server',
      };
      this.Messages.push(this.newMessage); // הכנסת ההודעה למערך ההודעות ב-Vue
      this.$refs.bottomElement.scrollIntoView({ behavior: 'smooth' });
      this.GPTconversation()
  }
    },
    async fetchData(myAPItoGPT){
      await new Promise((resolve) => setTimeout(resolve, 15000)); // השהיית הפעולה למשך 15 שניות
      if(this.runing === true){
      axios.get(myAPItoGPT.address, {mode: 'no-cors',
      params: {
    q: this.Messages[this.Messages.length - 1 ].text
  }})
  .then(response => {
    this.newAnswer = {
        id: this.AnswerNumber,
        text: response.data,
        sender: this.lestSender.name,

      };
      this.Messages.push(this.newAnswer); // הכנסת ההודעה למערך ההודעות ב-Vue
      this.$refs.bottomElement.scrollIntoView({ behavior: 'smooth' });
      this.AnswerNumber += 2;
      this.lestSender.name = myAPItoGPT.name;
      this.lestSender.address = myAPItoGPT.address;
      this.$refs.bottomElement.scrollIntoView({ behavior: 'smooth' });
      this.GPTconversation()

  })
  .catch(error => {
    console.log(error); // Handle the error
    let newAnswer = {
        id:  this.AnswerNumber,
        text: error,
        sender: 'server',
      };
      this.Messages.push(newAnswer); // הכנסת ההודעה למערך ההודעות ב-Vue
      this.$refs.bottomElement.scrollIntoView({ behavior: 'smooth' });
      this.MessageNumber += 2;
      this.lestSender.address = 'server';
  })
  }
},
GPTconversation(){
  if(this.lestSender.address === this.openAi2.address){
  this.fetchData(this.openAi1);
  this.OnWrite = false     
  }else if(this.lestSender.address === this.openAi1.address  || this.lestSender.address === 'server'){
    this.fetchData(this.openAi2);
  }else{
    console.log('the server has been discontinued!')
  }
},
WriteMessage(){
  this.OnWrite = true;
  if(this.text === 'Type a message here to start'){
        this.text = "";
      }
       
      },

clearMessage () {
        this.text = '';
        this.OnWrite = true;
      },
    },
    mounted(){
      },
  
};
</script>

